<template>
  <div class="dashboard">
    <a-page-header
      title="仪表盘"
      sub-title="系统概览"
    />
    
    <div class="dashboard-content">
      <a-row :gutter="16">
        <a-col :span="6">
          <a-card title="提示词模板" :bordered="false">
            <template #extra><a href="/prompts">管理</a></template>
            <p>{{ templateCount }} 个模板</p>
            <p>{{ categoryCount }} 个分类</p>
          </a-card>
        </a-col>
        
        <a-col :span="6">
          <a-card title="知识库" :bordered="false">
            <template #extra><a href="/knowledge">管理</a></template>
            <p>{{ knowledgeBaseCount }} 个知识库</p>
            <p>{{ documentCount }} 个文档</p>
          </a-card>
        </a-col>
        
        <a-col :span="6">
          <a-card title="API 调用" :bordered="false">
            <template #extra><a href="/api">查看</a></template>
            <p>今日: {{ apiCallsToday }}</p>
            <p>总计: {{ apiCallsTotal }}</p>
          </a-card>
        </a-col>
        
        <a-col :span="6">
          <a-card title="系统状态" :bordered="false">
            <p>模型状态: <a-badge status="success" text="在线" /></p>
            <p>API 服务: <a-badge status="success" text="在线" /></p>
          </a-card>
        </a-col>
      </a-row>
      
      <a-divider />
      
      <a-row :gutter="16">
        <a-col :span="12">
          <a-card title="最近活动" :bordered="false">
            <a-list size="small" :bordered="false" :data-source="recentActivities">
              <template #renderItem="{ item }">
                <a-list-item>
                  <template #actions>
                    <a>查看</a>
                  </template>
                  <a-list-item-meta :title="item.title">
                    <template #description>
                      {{ item.time }}
                    </template>
                  </a-list-item-meta>
                </a-list-item>
              </template>
            </a-list>
          </a-card>
        </a-col>
        
        <a-col :span="12">
          <a-card title="提示词使用情况" :bordered="false">
            <div style="height: 200px; display: flex; align-items: center; justify-content: center;">
              <p>此处显示提示词使用统计图表</p>
            </div>
          </a-card>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';

export default {
  name: 'Dashboard',
  setup() {
    // 模拟数据
    const templateCount = ref(0);
    const categoryCount = ref(0);
    const knowledgeBaseCount = ref(0);
    const documentCount = ref(0);
    const apiCallsToday = ref(0);
    const apiCallsTotal = ref(0);
    
    const recentActivities = ref([
      { title: '更新了聊天提示词模板', time: '2023-12-15 14:30' },
      { title: '添加了新的知识库文档', time: '2023-12-15 11:20' },
      { title: '重置了默认提示词', time: '2023-12-14 16:45' },
      { title: '优化了搜索相关提示词', time: '2023-12-14 10:15' },
      { title: '更新了系统配置', time: '2023-12-13 09:30' }
    ]);
    
    // 模拟数据加载
    const loadDashboardData = async () => {
      // 这里应该调用真实的 API 获取数据
      templateCount.value = 16;
      categoryCount.value = 4;
      knowledgeBaseCount.value = 3;
      documentCount.value = 27;
      apiCallsToday.value = 128;
      apiCallsTotal.value = 5763;
    };
    
    onMounted(() => {
      loadDashboardData();
    });
    
    return {
      templateCount,
      categoryCount,
      knowledgeBaseCount,
      documentCount,
      apiCallsToday,
      apiCallsTotal,
      recentActivities
    };
  }
};
</script>

<style scoped>
.dashboard {
  padding: 24px;
}

.dashboard-content {
  background: #fff;
  padding: 24px;
  margin-top: 24px;
  border-radius: 4px;
}
</style> 