<template>
  <div id="app">
    <a-config-provider :locale="zhCN">
      <a-layout style="min-height: 100vh">
        <a-layout-header class="header">
          <div class="logo">设计助手管理系统</div>
          <a-menu
            v-model:selectedKeys="selectedKeys"
            theme="dark"
            mode="horizontal"
            :style="{ lineHeight: '64px' }"
          >
            <a-menu-item key="dashboard">
              <router-link to="/">仪表盘</router-link>
            </a-menu-item>
            <a-menu-item key="prompt">
              <router-link to="/prompts">提示词管理</router-link>
            </a-menu-item>
            <a-menu-item key="knowledge">
              <router-link to="/knowledge">知识库管理</router-link>
            </a-menu-item>
            <a-menu-item key="api">
              <router-link to="/api">API 测试</router-link>
            </a-menu-item>
          </a-menu>
        </a-layout-header>
        
        <a-layout-content style="padding: 0 50px">
          <router-view />
        </a-layout-content>
        
        <a-layout-footer style="text-align: center">
          设计助手管理系统 ©2023 Created with Vue and Ant Design
        </a-layout-footer>
      </a-layout>
    </a-config-provider>
  </div>
</template>

<script>
import { ref } from 'vue';
import { useRoute } from 'vue-router';
import zhCN from 'ant-design-vue/es/locale/zh_CN';

export default {
  name: 'App',
  setup() {
    const route = useRoute();
    const selectedKeys = ref(['dashboard']);
    
    // 根据当前路由更新选中的菜单项
    const updateSelectedMenu = () => {
      const path = route.path;
      if (path === '/') {
        selectedKeys.value = ['dashboard'];
      } else if (path.startsWith('/prompts')) {
        selectedKeys.value = ['prompt'];
      } else if (path.startsWith('/knowledge')) {
        selectedKeys.value = ['knowledge'];
      } else if (path.startsWith('/api')) {
        selectedKeys.value = ['api'];
      }
    };
    
    // 监听路由变化
    updateSelectedMenu();
    
    return {
      selectedKeys,
      zhCN
    };
  }
};
</script>

<style>
.header {
  display: flex;
  align-items: center;
}

.logo {
  width: 200px;
  height: 31px;
  color: white;
  font-size: 18px;
  font-weight: bold;
  margin-right: 30px;
  display: flex;
  align-items: center;
}

#app {
  font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style> 